.tu-slider::before,
.tu-slider::after {
  display: table;
  content: ""; }

.tu-slider::after {
  clear: both; }

.tu-slider__runway {
  width: 100%;
  height: 8px;
  margin: 16px 0;
  background-color: #e4e7ed;
  border-radius: 4px;
  position: relative;
  cursor: pointer;
  vertical-align: middle;
  box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
  .tu-slider__runway.show-input {
    margin-right: 160px;
    width: auto; }
  .tu-slider__runway.disabled {
    cursor: default; }
    .tu-slider__runway.disabled .tu-slider__bar {
      background: linear-gradient(-1deg, #9baacf 0%, #bec8e4 50%, #c8d0e7 100%); }
    .tu-slider__runway.disabled .tu-slider__button {
      border-color: #bec8e4; }
    .tu-slider__runway.disabled .tu-slider__button-wrapper:hover, .tu-slider__runway.disabled .tu-slider__button-wrapper.hover {
      cursor: not-allowed; }
    .tu-slider__runway.disabled .tu-slider__button-wrapper.dragging {
      cursor: not-allowed; }
    .tu-slider__runway.disabled .tu-slider__button:hover, .tu-slider__runway.disabled .tu-slider__button.hover, .tu-slider__runway.disabled .tu-slider__button.dragging {
      transform: scale(1); }
    .tu-slider__runway.disabled .tu-slider__button:hover, .tu-slider__runway.disabled .tu-slider__button.hover {
      cursor: not-allowed; }
    .tu-slider__runway.disabled .tu-slider__button.dragging {
      cursor: not-allowed; }

.tu-slider__input {
  float: right;
  margin-top: 3px;
  width: 130px; }
  .tu-slider__input.tu-input-number--mini {
    margin-top: 5px; }
  .tu-slider__input.tu-input-number--medium {
    margin-top: 0; }
  .tu-slider__input.tu-input-number--large {
    margin-top: -2px; }

.tu-slider__bar {
  height: 8px;
  background: linear-gradient(-1deg, #510ccf 0%, #6d5dfc 50%, #8abdff 100%);
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  position: absolute; }

.tu-slider__button-wrapper {
  height: 36px;
  width: 36px;
  position: absolute;
  z-index: 1001;
  top: -15px;
  transform: translateX(-50%);
  background-color: transparent;
  text-align: center;
  user-select: none;
  line-height: normal; }
  .tu-slider__button-wrapper::after {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle; }
  .tu-slider__button-wrapper .tu-tooltip {
    vertical-align: middle;
    display: inline-block; }
  .tu-slider__button-wrapper:hover, .tu-slider__button-wrapper.hover {
    cursor: grab; }
  .tu-slider__button-wrapper.dragging {
    cursor: grabbing; }

.tu-slider__button {
  position: relative;
  width: 18px;
  height: 18px;
  background-color: #ffffff;
  border-radius: 50%;
  transition: 0.2s;
  user-select: none;
  box-shadow: 0 1px 3px 0px #c8d0e7; }
  .tu-slider__button::after {
    content: "";
    position: absolute;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    box-shadow: inset 1px 1px 4px #e4ebf5, inset -2px -2px 5px #ffffff;
    margin: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .tu-slider__button:hover, .tu-slider__button.hover, .tu-slider__button.dragging {
    transform: scale(1.2); }
  .tu-slider__button:hover, .tu-slider__button.hover {
    cursor: grab; }
  .tu-slider__button.dragging {
    cursor: grabbing; }

.tu-slider__stop {
  position: absolute;
  height: 8px;
  width: 8px;
  border-radius: 100%;
  background: rgba(255, 255, 255, 0.38);
  transform: translateX(-50%); }

.tu-slider__marks {
  top: 0;
  left: 12px;
  width: 18px;
  height: 100%; }
  .tu-slider__marks-text {
    position: absolute;
    transform: translateX(-50%);
    font-size: 14px;
    color: #909399;
    margin-top: 15px; }

.tu-slider.is-vertical {
  position: relative; }
  .tu-slider.is-vertical .tu-slider__runway {
    width: 8px;
    height: 100%;
    margin: 0 16px; }
  .tu-slider.is-vertical .tu-slider__bar {
    width: 8px;
    height: auto;
    border-radius: 0 0 3px 3px; }
  .tu-slider.is-vertical .tu-slider__button-wrapper {
    top: auto;
    left: -15px;
    transform: translateY(50%); }
  .tu-slider.is-vertical .tu-slider__stop {
    transform: translateY(50%); }
  .tu-slider.is-vertical.tu-slider--with-input .tu-slider__input {
    overflow: visible;
    float: none;
    position: absolute;
    bottom: 22px;
    width: 36px;
    margin-top: 15px; }
    .tu-slider.is-vertical.tu-slider--with-input .tu-slider__input .tu-input__inner {
      text-align: center;
      padding-left: 5px;
      padding-right: 5px; }
    .tu-slider.is-vertical.tu-slider--with-input .tu-slider__input .tu-input-number__decrease,
    .tu-slider.is-vertical.tu-slider--with-input .tu-slider__input .tu-input-number__increase {
      top: 28px;
      margin-top: -1px;
      line-height: 20px;
      box-sizing: border-box;
      transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1); }
    .tu-slider.is-vertical.tu-slider--with-input .tu-slider__input .tu-input-number__decrease {
      width: 18px;
      right: 18px;
      border-bottom-left-radius: 4px; }
    .tu-slider.is-vertical.tu-slider--with-input .tu-slider__input .tu-input-number__increase {
      width: 19px;
      border-bottom-right-radius: 4px; }
      .tu-slider.is-vertical.tu-slider--with-input .tu-slider__input .tu-input-number__increase ~ .tu-input .tu-input__inner {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0; }
  .tu-slider.is-vertical .tu-slider__marks-text {
    margin-top: 0;
    left: 15px;
    transform: translateY(50%); }
